.NavBar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: row wrap;
    background: url(../imagens/background.jpg), #a3a5ba;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: center;
}
.NavBar nav{
    margin: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Search{
    padding: 10px;
    outline: 0;
    border: 0;
    border-radius: 10px 0 0 10px;
}
.NavBar nav button{
    border-radius: 0 10px 10px 0;
    border: 0;
    padding: 6px;
    cursor: pointer;
}
@media screen and (max-width:660px) {
    .NavBar{
        justify-content: center;
    }
}
@media screen and (max-width:360px) {
    .NavBar figure img{
        width: 200px;
    }
}