.container{
    background-color: #ffffff56;
    display: inline-flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 250px;
    height: 350px;
    margin: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px #0000001e;
}
.container h1, p{
    margin: 0;
    padding: 0;
}
.container img{
    transition: 1s;
    cursor: pointer;
}
.container img:hover{
    width: 120px;
}
.infoCharacter{
    background-color: #fff;
    width: 100%;
    text-align: center;
    border-radius: 0 0 10px 10px;
    padding: 0 0 10px 0;
}